}

.c-see-more-btn {
  display: inline-block;
  position: relative;
  padding: 0;
  font-size: 0.9rem;
  line-height: 1;
}

.c-see-more-btn::after {
  content: "";
  display: block;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 100%;
  background-color: #24292E;
}

